* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  height: 100vh;
  background: linear-gradient(-45deg, #ee7752, #6a203c, #0c81ab, #391cbf) 0 0 / 400% 400%;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  animation: gradient 15s infinite;
  display: flex;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

input {
  max-width: 320px;
  font: inherit;
  outline-offset: 3px;
  background-color: #e2e2e2;
  border: none;
  border-radius: 10px;
  outline: 2px solid #febf00;
  margin-bottom: 24px;
  margin-right: 15px;
  padding: 10px 1rem;
  font-size: 15px;
  transition: all .25s;
}

input:focus {
  outline-offset: 5px;
  background-color: #fff;
}

button {
  color: #fff;
  width: 9em;
  height: 3em;
  text-align: center;
  z-index: 1;
  background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4) 0 0 / 300%;
  border: none;
  border-radius: 30px;
  font-family: inherit;
  font-size: 14px;
  line-height: 2em;
  text-decoration: none;
  position: relative;
}

button:hover {
  border: none;
  animation: ani 8s linear infinite;
}

@keyframes ani {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 400%;
  }
}

button:before {
  content: "";
  z-index: -1;
  background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4) 0 0 / 400%;
  border-radius: 35px;
  transition: all 1s;
  position: absolute;
  top: -5px;
  bottom: -5px;
  left: -5px;
  right: -5px;
}

button:hover:before {
  filter: blur(20px);
}

button:active {
  background: linear-gradient(32deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
}

